const dateformat = require('dateformat');

module.exports = (s, u) => {
  if (typeof s === 'string') {
    const match = s.match(/^([0-9]{4})-([0-9]{2})-([0-9]{2})/)

    if (!match) {
      throw new Error("Invalid date: " + s)
    }

    s = new Date(
      parseInt(match[1]),
      parseInt(match[2]),
      parseInt(match[3])
    )
  }

  return dateformat(s, u)
};



